.item {
    display: flex;
    flex-direction: row;

    gap: 10px;

    align-items: center;
    text-decoration: none;

    text-wrap: nowrap;

    min-width: fit-content;
    padding: 10px 20px;

    transition: all 0.15s ease-in-out;

    border-radius: 5px;

    &:hover {
        background-color: rgba(255, 255, 255, 0.1);
    }

    img {
        height: 20px;

        border-radius: 4px;
    }

    &_title {
        font-size: 16px;
        font-weight: 600;
        text-transform: capitalize;
        color: white;
    }
}